import React from 'react';
import {
  BasicCommentItem,
  BasicCommentRequest,
  RenderPopupProps,
} from '../../types';
import CommentList from './commentList';
import TextInput from './textInput';

type SimplePopupProps = RenderPopupProps & {
  sendComment?: (req: BasicCommentRequest) => Promise<boolean>;
  getCommentList?: (contentId: string) => Promise<BasicCommentItem[]>;
};
export default function SimplePopup(props: SimplePopupProps) {
  const { isNew, contentId, onAdd , sendComment, getCommentList } = props;
  if (isNew) {
    return <TextInput onAdd={onAdd} contentId={contentId} sendComment={sendComment} />;
  } else {
    return (
      <CommentList contentId={contentId} getCommentList={getCommentList} />
    );
  }
}
